import { createStyles } from '@instacart/cocktail'
import AssetImage from '@app/react/common/AssetImage'
import { useConfig } from '@app/services/config/hooks'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import ExpressPoweredBy from './ExpressPoweredBy'
import ExpressTermsAndConditions from './ExpressTermsAndConditions'

const useStyles = createStyles(tokens => ({
  banner: {
    maxHeight: '300px',
    maxWidth: '100%',
    minWidth: '100%',
    objectFit: 'cover',
  },
  disclaimer: {
    flex: 2,
    fontSize: '11px',
    lineHeight: '17px',
    padding: '5px 30px',
    textAlign: 'left',
  },
  footer: {
    marginBottom: '-5px',
  },
  poweredBy: {
    alignItems: 'center',
    display: 'flex',
    flex: 1,
    fontSize: `${tokens.size.spacing.xxSmall}px`,
    fontWeight: 'bold',
    justifyContent: 'center',
    lineHeight: '22px',
    padding: '5px',
  },
  topRow: {
    alignItems: 'center',
    borderTop: `1px solid ${tokens.color.neutral.grey2}`,
    color: tokens.color.neutral.grey4,
    display: 'flex',
    fontFamily: tokens.font.family.primary,
    justifyContent: 'space-around',
    marginTop: '30px',
    padding: '30px',
  },
  topRowMobile: {
    flexDirection: 'column',
  },
}))

interface ExpressFooterProps {
  showText?: boolean
  showImage?: boolean
  poweredByText?: string
}

const ExpressFooter = ({
  showText = true,
  showImage = true,
  poweredByText,
}: ExpressFooterProps) => {
  const { icExpress } = useConfig()
  const isMobile = useIsMobile()

  const styles = useStyles()

  return (
    <div css={styles.footer}>
      {showText && (
        <div css={[styles.topRow, isMobile && styles.topRowMobile]}>
          <div css={styles.poweredBy}>
            {showImage && <ExpressPoweredBy poweredByText={poweredByText} />}
          </div>

          {!icExpress.giftCardOnlyRegistration && (
            <div css={styles.disclaimer}>
              <ExpressTermsAndConditions />
            </div>
          )}
        </div>
      )}

      {showImage && icExpress.footer.image && (
        <AssetImage css={styles.banner} src={icExpress.footer.image} alt="" />
      )}
    </div>
  )
}

export default ExpressFooter
