import { createStyles } from '@instacart/cocktail'
import { useTermsAndConditions } from '@app/services/express/hooks'

const useStyles = createStyles(() => ({
  terms: {
    a: {
      color: '#333333',
      textDecoration: 'underline',
    },
  },
}))

const ExpressTermsAndConditions = () => {
  const termsAndConditions = useTermsAndConditions()

  const styles = useStyles()

  return (
    <div
      css={styles.terms}
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{
        __html: termsAndConditions,
      }}
    />
  )
}

export default ExpressTermsAndConditions
